import Vue from 'vue'
import Router from 'vue-router'
import VueHead from 'vue-head'

import Index from '@/components/Index'
import Contact from '@/components/Contact'
import Products from '@/components/Products'

Vue.use(VueHead)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/contact',
      name : 'Contact',
      component : Contact
    },
    {
      path : '/products',
      name : 'Products',
      component : Products
    }
  ]
})



