<template>
  <div id="AlertNotif" :class="[{'active' : display},'alert',typeClass]">
    {{ message }} <span class="closeBtn" @click="display = false">close</span>
  </div>
</template>

<script>
  export default {
    name : "AlertNotif",
    data: () => ({
      message : '',
      typeClass : '',
      //Hide the alert by default
      display : false
    }),
    created() {

      //This component listen for any brodcasted message
      //If any component throws a message
      //This component will show up

      const vm = this;
      const timeInSec = 2.5;

      Event.$on('alertNotif', function(data){
        vm.message = data.message;
        vm.typeClass = data.type;
        vm.display = true;

        //Hide the alert after timeInSec
        setTimeout(function() {
          vm.display = false;
        }, timeInSec * 1000);

      });
    }
  }
</script>

<style lang="scss" scoped>
  #AlertNotif{
    position: fixed;
    top: 15px;
    width: auto;
    left: 50%;
    text-align: center;
    font-size: 14px;
    z-index: 1021;
    transform: translate(-50%);
    background-color: #fff;
    box-shadow: 0 3px 4px rgba(0,0,0,0.05);
    border: none;
    transition: all 0.3s ease;
    opacity: 0;

    .closeBtn{
      margin-left: 20px;
      opacity: 0.6;
      font-size: 12px;
      transition: all 0.3s ease;
      cursor: pointer;

      &:hover{
        opacity: 0.9;
      }
    }

    &.active{
      top: 30px;
      opacity: 1;
    }
    

    &.success{
      border-left: solid 3px #33c787;
    }
    &.success:before{
      content: "\f00c";
      font-family: FontAwesome;
      margin-right: 5px;
      color: #33c787;
    }

    &.danger{
      border-left: solid 3px #ed4014;
    }
    &.danger:before{
      content: "\f00d";
      font-family: FontAwesome;
      margin-right: 5px;
      color: #ed4014;
    }

    &.warning{
      border-left: solid 3px #ff9800;
    }
    &.warning:before{
      content: "\f071";
      font-family: FontAwesome;
      margin-right: 5px;
      color: #ff9800;
    }
  }
</style>



