<template>
  <div id="Contact">

     <section class="section white">
      <div class="container">
        <div class="row">
          <div class="col-lg-6 col-md-6 col-sm-12 col-xs-17">
            <h1>Have an idea?</h1>
            <h3>Let's talk!</h3>
            <br><br>
            
            <p><i class="fa fa-phone"></i>&nbsp;&nbsp;&nbsp;+91 (720) 626 4120</p>
            <p><a href="mailto:info@techies.pro"><i class="fa fa-envelope"></i>&nbsp;&nbsp;&nbsp;info@techies.pro</a></p>
            <p>
              <a href="" class="social"><i class="fa fa-twitter"></i></a>
              <a href="" class="social"><i class="fa fa-instagram"></i></a>
              <a href="" class="social"><i class="fa fa-facebook"></i></a>
            </p>
            

          </div>
          <div class="col-lg-6 col-md-6 col-sm-12 col-xs-17">
            <h2>Leave us a message</h2>
            <form action="https://formspree.io/info@techies.pro" method="POST" class="form-row mt-5">
              <input type="hidden" name="_subject" value="New Message!" />
              <div class="form-group col-md-6">
                <label for="name">Name <span class="text-info">*</span></label>
                <input type="text" name="name" placeholder="Name" class="form-control" required v-model="data.name">
              </div>

              <div class="form-group col-md-6">
                <label for="email">Email <span class="text-info">*</span></label>
                <input type="email" name="_replyto" placeholder="Email" class="form-control" required v-model="data.email">
              </div>

              <div class="form-group col-6">
                <label for="email">Phone Number</label>
                <input type="phone" name="phoneNumber" placeholder="Phone Number" class="form-control">
              </div>

              <div class="form-group col-6">
                <label for="email">Service</label>
                <select name="Service" id="" class="form-control">
                  <option value="No Service selected">Select Service</option>
                  <option value="Web App Development">Web App Development</option>
                  <option value="Mobile App Development">Mobile App Development</option>
                  <option value="PWA Development">PWA Development</option>
                  <option value="Wordpress to PWS">Wordpress to PWS</option>
                  <option value="Other">Other</option>
                </select>
                
              </div>

              <div class="form-group col-12">
                <label for="message">Message <span class="text-info">*</span></label>
                <textarea name="message" id="message" cols="30" required class="form-control" rows="4" v-model="data.message" placeholder="Message"></textarea>
              </div>
              
              <br>
              <button type="submit" class="btn btn-secondary">Send Message</button>
            </form>
          </div>
          
        </div>
      </div>
    </section>

  </div>
</template>

<script>
export default {
  name : "Contact",
  data: () => ({
    data : {
      name : '',
      email : '',
      message : ''
    }
  }),
  method : {
    validate: () => {
      alert('form is submitting!');
    }
  },
  head: {
    title : function(){
      return {
        inner : 'Contact - Techies.pro'
      }
    }
  }
}
</script>


<style lang="scss" scoped>
.social{
  font-size: 24px;
  color: rgba(0,0,0,0.6);
  margin-right: 10px;

  &:hover{
    color: #6ebd6b;
  }
}
</style>




